<template>
  <div>
    <div class="p3 flex-row">
      <a class="tertiary font-title" @click="$router.back()"><i class="fas fa-chevron-left m1-right"></i>BACK</a>
      <div class="flex-one"></div>
      <div class="p1-bottom p1-left border-bottom-secondary">{{client.name}}</div>
    </div>

    <div class="p3 p0-top">
      <div v-if="property" class="m4-bottom">
        <p class="font-title light font3">{{property.description}}</p>
        <p class="tertiary line180">{{property.city}}, {{property.state}} ({{property.county}})</p>
      </div>

      <button class="button alt h-fill m1-bottom" @click="createAppointmentRequest"><i class="fas fa-plus-circle m1-right"></i>ADD SHOWING</button>

      <div class="appointments">
        <div v-for="(appointment, i) in appointments" :key="i" class="appointment-wrapper" :class="[appointment.status]">
          <title-label :title="appointment.status.toUpperCase()" class="m3-top"></title-label>
          <appointment :appointment="appointment" mode="realtor" class="m2-bottom" @change="apptChanged"></appointment>
        </div>
        <p v-if="appointments.length === 0" class="tertiary m4-top text-center font3">No appointments</p>
      </div>
    </div>

    <div class="p3">
      <hr class="m5-top m5-bottom">
      <button class="button alt primary" @click="deleteUser"><i class="fas fa-times m1-right primary"></i><value-transition :value="deletePrompt" name="fade" /></button>
      <transition class="fade">
        <button v-if="confirmingDelete" class="button alt m2-top" @click="cancelDelete">CANCEL</button>
      </transition>
    </div>

    <transition name="slide-up">
      <div v-if="prompt" class="prompt fixed bg-base z2 shadow p3 flex-column">
        <div class="m4-bottom text-right">
          <a class="tertiary font-title" @click="prompt = false"><i class="fas fa-times m1-right"></i>CANCEL</a>
        </div>

        <create-appointment :client-id="clientId" :done="dismissPrompt" class="flex-one"></create-appointment>
      </div>
    </transition>
  </div>
</template>

<script>
import TitleLabel from '@/components/TitleLabel'
import Appointment from '@/views/Appointment'
import CreateAppointment from '@/views/forms/CreateAppointment'
import { mapGetters } from 'vuex'

export default {
  name: 'RealtorClient',
  data () {
    return {
      prompt: false,
      appointments: [],
      property: undefined,
      confirmingDelete: false
    }
  },
  props: ['clientId'],
  computed: {
    ...mapGetters(['clients', 'session']),
    client () {
      return this.clients.find(c => c.id === parseInt(this.clientId))
    },
    deletePrompt () {
      return this.confirmingDelete ? 'CONFIRM DELETE' : `DELETE ${this.client.name}`
    }
  },
  inject: ['userClient', 'realtorClient'],
  components: { TitleLabel, Appointment, CreateAppointment },
  methods: {
    fetchAppointments () {
      const self = this
      this.userClient.getAppointments(this.clientId)
        .then(appointments => {
          window.appts = appointments
          self.appointments = appointments
        })
    },
    createAppointmentRequest () {
      this.prompt = true
    },
    dismissPrompt () {
      this.fetchAppointments()
      this.prompt = false
    },
    deleteUser () {
      if (this.confirmingDelete) {
        const self = this
        this.realtorClient.deleteUser(this.clientId, this.session)
          .then(r => {
            self.$router.push('/realtor')
          })
      } else {
        this.confirmingDelete = true
      }
    },
    cancelDelete () {
      this.confirmingDelete = false
    },
    apptChanged (updatedAppointment) {
    }
  },
  mounted () {
    const self = this
    this.fetchAppointments()
    this.userClient.getProperty(this.clientId)
      .then(property => {
        self.property = property
      })
  }
}
</script>

<style lang="scss" scoped>
.appointments {
  .pending ~ .pending, .scheduled ~ .scheduled, .declined ~ .declined, .completed ~ .completed, .canceled ~ .canceled {
    .title-label {
      display: none;
    }
  }
}
</style>
